import './main.css';
import { Elm } from './Main.elm';
import * as serviceWorker from './serviceWorker';

var app = Elm.Main.init({
	node: document.getElementById('root'),
	flags: settings
});

const defaultLongitude = 120.680906;	//系統預設坐標(台中文化資產園區)
const defaultLatitude = 24.133073;

var mymap = L.map('mainMap').setView([defaultLatitude, defaultLongitude], 8);
L.tileLayer('https://wmts.nlsc.gov.tw/wmts/PHOTO2/default/GoogleMapsCompatible/{z}/{y}/{x}').addTo(mymap);

// popup 置中
mymap.on('popupopen', function (e) {
	var px = mymap.project(e.target._popup._latlng); // find the pixel location on the map where the popup anchor is
	px.y -= e.target._popup._container.clientHeight / 2; // find the height of the popup container, divide by 2, subtract from the Y axis of marker location
	mymap.panTo(mymap.unproject(px), { animate: true }); // pan to new center
});

// 開啟告警事件&災害回報，隱藏map popup
$(document).on('show.bs.collapse', '#alert, #bulletin', function () {
	mymap.closePopup();
})

var markerGroup;

app.ports.setView.subscribe(function (data) {
	console.log(data);
	mymap.setView.apply(mymap, data);
});

app.ports.setMarkers.subscribe(function (data) {
	console.log(data);

	const markers = data.map(function (m) {
		const opts = {
			stroke: false,
			fill: true,
			fillOpacity: 1.0,
			fillColor: m.color
		};
		let c = L.circleMarker(m.pos, opts);
		c.bindPopup(m.popup, {
			autoClose: false,
			className: 'map-pop'
		}).addTo(mymap);
		return c;
	});
	if (markerGroup) {
		markerGroup.remove();
	}
	markerGroup = L.layerGroup(markers).addTo(mymap);
});

app.ports.setLastModified.subscribe(function (data) {
	console.log(data);
	let ts = new Date(data);
	console.log(ts);
	let y = ts.getFullYear();
	let m = ts.getMonth() + 1;
	let d = ts.getDate();
	let res = y + "年" + m + "月" + d + "日";
	// console.log(res);
	$("#updatetime").text(res);
});

